<template>
  <div class="add">
      <el-form ref="form" :model="formData" label-width="80px">
          <el-form-item label="">
              <el-input v-model="formData.title"></el-input>
          </el-form-item>
          <el-form-item label="">
              <Editor v-model="formData.content" @getTxt="getText"></Editor>
          </el-form-item>
          <el-form-item>
              <el-button v-if="$route.name == ''" @click="upData"></el-button>
              <el-button v-if="$route.name == ''" @click="update"></el-button>
          </el-form-item>
      </el-form>
  </div>
</template>
<script>
import Editor from "@/components/editor";
export default {
  data() {
      return {
          formData:{
              title:"",
              content:"",
              contentTxt:"",
              id:""
          }
      }
  },
  components: {
    Editor
  },
  methods:{
    getText(val) {
      this.formData.contentTxt = val;
    },
    upData() {
      this.$axios.post("addPingyi", this.formData).then(res => {
        // console.log(res.data)
        if (res.ret) {
          this.$message({
            message: '',
            type: 'success'
          });
          setTimeout(() => {
            this.$router.push('/pingyi/list')
          },1000)
        }else{
          this.$message.error('');
        }
      });
    },
    update() {
        this.formData.id = this.$route.query.id;
      this.$axios.post("updatePingyi", this.formData).then(res => {
        if (res.ret) {
          this.$message({
            message: '',
            type: 'success'
          });
          setTimeout(() => {
            this.$router.push('/pingyi/list')
          },1000)
        }else{
          this.$message.error('');
        }
      });
    },
  },
  created() {
    if (this.$route.name == "") {
      this.$axios.get("getPingyi",{id:this.$route.query.id}).then(res => {
        this.formData = res.data[0];
        // console.log(res.data)
      });
    }
  },
  watch: {
    $route(to, from, next) {
      if (to.name == "") {
        this.formData = {
          title: "",
          content: "",
          contentText: ""
        };
      }
    }
  } 
}
</script>
<style scoped>
.add {
  margin-left: 230px;
  padding: 20px 20px 180px;
  margin-top: 40px;
}
</style>



